import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

export default function Skills() {
  const [ref, inView] = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1, // Faster stagger for smaller items
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.5 } },
  };

  const categorizedSkills = {
    Frontend: ["React", "Vite", "Figma"],
    Backend: ["Django", "Python", "Streamlit", "MySQL", "PostgreSQL"],
    "AI/ML": ["PyTorch", "TensorFlow", "Ollama", "Hugging Face", "Replicate API"],
    DevOps: ["Docker", "Kubernetes", "AWS", "GCP", "NCP", "Vercel"],
    Automation: ["n8n", "Airflow", "Celery", "API Integration"],
  };

  return (
    <section id="skills" className="bg-gray-900 py-16 px-4 text-white dark:bg-gray-800">
      <div className="max-w-6xl mx-auto">
        <motion.h2
          className="text-4xl font-bold text-center mb-12"
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          Skills
        </motion.h2>
        <motion.div
          ref={ref}
          variants={containerVariants}
          initial="hidden"
          animate={inView ? "visible" : "hidden"}
          className="grid md:grid-cols-2 lg:grid-cols-3 gap-8"
        >
          {Object.entries(categorizedSkills).map(([category, skills]) => (
            <motion.div
              key={category}
              variants={itemVariants}
              className="relative bg-white/10 backdrop-blur-md p-6 rounded-lg shadow-lg border border-gray-700"
            >
              <h3 className="text-2xl font-semibold text-blue-400 mb-4">{category}</h3>
              <div className="flex flex-wrap gap-3">
                {skills.map((skill) => (
                  <span
                    key={skill}
                    className="bg-gray-700 text-gray-200 px-4 py-2 rounded-full text-md font-medium hover:bg-blue-600 transition-colors duration-200"
                  >
                    {skill}
                  </span>
                ))}
              </div>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </section>
  );
}
